/*******************************************************************************
 * Hover effects for enhanced interactivity
 ******************************************************************************/

// Profile picture hover effect
.profile img {
 transition: all 0.3s ease;
 
 &:hover {
 transform: scale(1.05);
 box-shadow: 0 8px 20px rgba(220, 20, 60, 0.3); // Ferrari red glow
 }
}

// CV section cards hover effects
.cv .card {
 transition: all 0.3s ease;
 cursor: pointer;
 
 &:hover {
 transform: translateY(-5px);
 box-shadow: 0 12px 25px rgba(0, 0, 0, 0.15);
 border-left: 4px solid var(--global-theme-color); // Ferrari red accent
 }
}

// Dark mode CV card hover
html[data-theme='dark'] .cv .card:hover {
 box-shadow: 0 12px 25px rgba(0, 0, 0, 0.4);
 background: rgba(255, 255, 255, 0.15) !important; // Keep translucent
}

// Blog post cards hover effects
.post-entry, 
.post-preview,
article.post-preview,
.post-list li {
 transition: all 0.3s ease;
 border-radius: 8px;
 padding: 1.5rem;
 margin-bottom: 1.5rem;
 
 &:hover {
 transform: translateY(-5px);
 box-shadow: 0 12px 25px rgba(0, 0, 0, 0.15);
 border-left: 4px solid var(--global-theme-color); // Ferrari red accent
 }
}

// Dark mode blog post hover
html[data-theme='dark'] .post-entry:hover,
html[data-theme='dark'] .post-preview:hover,
html[data-theme='dark'] article.post-preview:hover,
html[data-theme='dark'] .post-list li:hover {
 box-shadow: 0 12px 25px rgba(0, 0, 0, 0.4);
}

// Blog post links hover
.post-entry h3 a,
.post-preview h3 a,
.post-title a,
.post-list li h3 a {
 transition: all 0.3s ease;
 
 &:hover {
 color: var(--global-theme-color) !important;
 text-decoration: none;
 transform: translateX(5px); // Subtle slide effect
 }
}

// Blog post meta links hover - more specific selector
.post-list .post-tags a {
 transition: color 0.3s ease;
 
 &:hover {
 color: var(--global-theme-color) !important;
 }
}

// Ensure hashtag and calendar icons display properly
.post-tags i.fas,
.post-tags .fas,
.tag-list i.fas,
.tag-list .fas {
 display: inline !important;
 margin-right: 4px !important;
 opacity: 1 !important;
 visibility: visible !important;
 font-family: "Font Awesome 5 Free" !important;
 font-weight: 900 !important;
}

// Force hashtag icon to appear
.post-tags .fa-hashtag:before,
.tag-list .fa-hashtag:before {
 content: "#" !important;
}

// Alternative hashtag display
.post-tags a {
 position: relative;
}

.post-tags a .fa-hashtag {
 font-size: 0.8em !important;
}

// Blog header hover effects
.header-bar h1,
.header-bar h2 {
 transition: all 0.3s ease;
}

.header-bar:hover h1 {
 color: var(--global-theme-color);
 transform: translateY(-2px);
}

// Repository cards hover (if any)
.repo {
 transition: all 0.3s ease;
 
 &:hover {
 transform: translateY(-3px);
 box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
 }
}

// Smooth transitions for all interactive elements
a, button, .card, .post-entry {
 transition: all 0.3s ease;
}

// Enhanced social icons hover (building on existing)
.contact-icons a {
 transition: all 0.3s ease;
 
 &:hover {
 transform: translateY(-2px) scale(1.1);
 color: var(--global-theme-color) !important;
 }
}